import React, { useEffect, useState } from 'react';
import './App.css';
import './style2.css';
import 'antd/dist/antd.less';
import './styles.less';

import Search from './Search';
import Nav from './Nav';
import {Routes, Route, Link} from 'react-router-dom'
import Overview from './Overview';
import Home from './Home';
import Articles from './Articles';
import Help from './Help';
import Contact from './Contact';

import 'antd/dist/antd.css';

import { Layout } from 'antd';
const { Header, Sider, Content } = Layout;


const App = () => {

  
  const [isEng, setIsEng] = useState(true);
  const toggleLanguage = () => {
        setIsEng(isEng => !isEng);
    }

  const [curr, setCurr] = useState("");

  const setCurrMew = (val) => {
    setCurr(val);
  }

  const [color, setColor] = useState({
    primaryColor: '#1890ff',
    errorColor: '#ff4d4f',
    warningColor: '#faad14',
    successColor: '#52c41a',
    infoColor: '#1890ff',
  });
  

  return (
    <div className='App'>
      <Nav currState={curr} lang={isEng} click={toggleLanguage}/>
      <Routes>
        <Route exact path="/" element={<Home setCurr={setCurrMew} lang={isEng}/>} />
        <Route exact path="/Search" element={<Search setCurr={setCurrMew} lang={isEng}/>} />
        <Route exact path="/Overview" element={<Overview setCurr={setCurrMew} lang={isEng}/>} />
        <Route exact path="/Articles" element={<Articles setCurr={setCurrMew} lang={isEng}/>} />
        <Route exact path="/Help" element={<Help setCurr={setCurrMew} lang={isEng}/>} />
        <Route exact path="/Contact" element={<Contact setCurr={setCurrMew} lang={isEng}/>} />
      
      </Routes>
    </div>
  );
}

export default App;
