import React from "react";
import { Card, Rate } from 'antd';
import YoutubeEmbed from "./YoutubeEmbed";
import Icon, { StarFilled, YoutubeFilled } from '@ant-design/icons';


function Recording(props) {

    

    var rating = "<span>";

    for (var i=0; i<parseInt(props.rating); i++) {
        rating += "<StarFilled />";
    }

    const onClick = () => {
        props.setLink(props.link);
    }

    rating += "</span>"

    return (
        <div>
            <Card
                title= {props.name}
                extra={<span><Rate allowHalf defaultValue={props.rating} /></span>}
                // style={{
                //     width: 100%,
                // }}
            >
                <div>
                    <p>Year: {props.date}</p>
                    <YoutubeFilled onClick={onClick} style={{cursor:'pointer', fontSize:'40px', position:"absolute", right:"20px", top:"55%"}} />
                </div>
            </Card>
        </div>
    )
}

export default Recording