import React from "react";
import PropTypes from "prop-types";

// source code: https://dev.to/bravemaster619/simplest-way-to-embed-a-youtube-video-in-your-react-app-3bk2

function YoutubeEmbed ({ embedId }) {

  if (embedId === "") {
    return (<div></div>);
  } else {
    return (
      <div className="video-responsive" style={{marginTop:'50px'}}>
        <iframe width="912" height="513" src={"https://www.youtube.com/embed/"+embedId} frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>

      </div> )
  }
};

YoutubeEmbed.propTypes = {
  embedId: PropTypes.string.isRequired
};

export default YoutubeEmbed;
