import React, { useEffect } from "react";
import { Layout, Menu, Space, Typography } from "antd";
import Icon, { UserOutlined } from '@ant-design/icons';

import { Link } from "react-router-dom";
import './style2.css';
import Password from "antd/lib/input/Password";


const {Header} = Layout;

function Nav(props) {

    // console.log(props.lang);
    var l = props.lang ? "en" : "fr";

    console.log(l);

    // console.log(document.getElementById("over").className);
    // console.log(document.getElementById("sear").className);

    useEffect (() => {

        if(props.currState === "over") {
            document.getElementById("over").className = "ant-menu-item ant-menu-item-disabled ant-menu-item-selected";
            document.getElementById("sear").className = document.getElementById("sear").className.replace("ant-menu-item-selected", " ");
            document.getElementById("home").className = document.getElementById("home").className.replace("ant-menu-item-selected", " ");
    
        } else if (props.currState === "home") {
            console.log(document.getElementById("home").className);
            console.log(document.getElementById("sear").className);

            document.getElementById("home").className = "ant-menu-overflow-item ant-menu-item ant-menu-item-selected";
            document.getElementById("sear").className = document.getElementById("sear").className.replace("ant-menu-item-selected", " ");
            document.getElementById("over").className = document.getElementById("over").className.replace("ant-menu-item-selected", " ");
    
        } else if(props.currState === "sear") {
            document.getElementById("sear").className = "ant-menu-overflow-item ant-menu-item ant-menu-item-selected";
            document.getElementById("over").className = document.getElementById("over").className.replace("ant-menu-item-selected", " ");
            document.getElementById("home").className = document.getElementById("home").className.replace("ant-menu-item-selected", " ");
    
        } else {
            document.getElementById("sear").className = document.getElementById("sear").className.replace("ant-menu-item-selected", " ");
            document.getElementById("over").className = document.getElementById("over").className.replace("ant-menu-item-selected", " ");
            document.getElementById("home").className = document.getElementById("home").className.replace("ant-menu-item-selected", " ");
            
        }
    },[props.currState])
    
    return (
        <Layout >
            <Header>
                <Menu theme="dark" mode="horizontal" style={{position: 'fixed', left: '20px', width:'100%', zIndex:"1000"}}>
                    <Menu.Item id="home"><Link to="/">{props.lang ? "Home" : "Accueil"}</Link></Menu.Item>
                    <Menu.Item id="sear"><Link to="/Search">{props.lang ? "Search" : "Recherche"}</Link></Menu.Item>
                    <Menu.Item id="over" disabled="true">{props.lang ? "Overview" : "Overvue"}</Menu.Item>
                    <Menu.Item><Link to="/Articles">{props.lang ? "Articles" : "Articles"}</Link></Menu.Item>
                    <Menu.Item><Link to="/Help">{props.lang ? "Help" : "Aide"}</Link></Menu.Item>
                    <Menu.Item><Link to="/Contact">{props.lang ? "Contact Us" : "Contacter"}</Link></Menu.Item>
                </Menu>
            </Header>
            <Header style = {{position: 'fixed', right: '90px', zIndex:"1000"}}>
                <Typography.Paragraph idname="lang" onClick={props.click} style={{color: 'white', cursor:'pointer'}}>{props.lang ? "en" : "fr"}</Typography.Paragraph>
            </Header>
            <Header style = {{position: 'fixed', right: '15px', zIndex:"1000"}}>
                <UserOutlined style={{color: 'grey', fontSize: "20px"}}/>
            </Header>
        </Layout>
    );
}

export default Nav